import GlobalStyleSheet from '../../../../styleSheet';
import { Row } from 'react-bootstrap'

const Footer = () => (
    <div>
        <div style={styles.footer}>
            <p className="text-center" style={styles.fooeter_text}>
                Copyright@ 2020
                    <span style={{ fontSize: 'medium', fontWeight: 'bold', }}> Muhalik </span>
                - Online Shopping Website
            </p>
            <Row>

            </Row>
        </div>
    </div>
)

const styles = {
    footer: {
        background: `${GlobalStyleSheet.card_header_background}`,
        position: 'absolute',
        left: "0",
        bottom: '0',
        right: '0',
    },
    fooeter_text: {
        margin: '10px',
        fontSize: 'smaller',
        color: 'black'
    },
}

export default Footer;